@mixin getLstmStyles(){
.lstm_model{
  height: 100%;
  background-color: white;
  border-radius:2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 3px 3px 0 rgba(0,0,0,0.1);

  .header{
    background-color: darken(#0d88e6, 5%);
    border-top-left-radius:2rem;
    border-top-right-radius:2rem ;
    width: 100%;


  h1{color: white;
    text-align: center;
    margin-top: 0.6rem;
    font-size: 1.8rem;
    margin-bottom: 0.4rem;
    padding: 0.2rem 0;
    font-weight: 550;



  }
}

  .prediction{
    width: 100%;
    background-color: lighten(#0d88e6, 48%);


  h2{color: darken(#0d88e6, 1%);
    text-align: center;
    font-size: 1.5rem;
    padding: 0.3rem 0;

    span{
        text-decoration: underline;

    }
  }

  }

  }
}
