@mixin getIndicatorsStyles(){

  .indicators{

  height: 100%;

$baseColor: #0061A8;
$borderRadius: 2rem;
$padding: 10px;

.table-users {
   border-radius: $borderRadius;
   box-shadow: 3px 3px 0 rgba(0,0,0,0.1);
   overflow: hidden;
   width: 100%;
   height: 100%;
  background-color: white;

   .header {
   background-color: darken($baseColor, 1%);
   color: white;
   padding: 0.8rem;
   text-align: center;
   text-transform: uppercase;
   font-size: 1.7em;
    font-weight: 600;



   h1{

      font-size:1.8rem;
      font-weight: 600;
    }

}

table {
    width: 100%;



   td, th {
      padding: $padding;
      text-align: center;
   }


   td {
      vertical-align: middle;
      color:black;



      &:last-child {
         font-size: 0.95em;
         line-height: 1.4;
      }
   }

   .indic{
        font-weight: 700;
        text-align:center;
      }



   th {
      background-color: lighten($baseColor, 50%);
      font-weight: 800;
      font-size: 1.1rem;
      text-decoration: underline;
      text-align: center;
      color: darken($baseColor, 10%);

   }

   tr {
      &:nth-child(2n) { background-color: white; }
      &:nth-child(2n+1) { background-color: lighten($baseColor, 55%) }
   }
}

}

  }
}
