@mixin getTwitterStyles() {

  .twitter{

    height:80%;
    background-color: white;
    border-radius: 2rem;
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 3px 3px 0 rgba(0,0,0,0.1);

   .header{
     background-color: darken(#0d88e6, 1%);
     padding: 0.4rem;
     width: 100%;
     border-top-left-radius: 2rem;
     border-top-right-radius: 2rem;


   .title{

    color:white;
    text-align: center;
    margin-top: 0.2rem;
    font-size: 2rem;
    font-weight: 540;
   }
  }

   .logo{

    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 0.8rem;


    img{
      height:2.5rem;
      margin-top: 0.3rem;

    }


   }

   .posneg{
     display: flex;
     flex-direction: row;
     padding: 1rem;
     gap: 1.5rem;
     color:#0d88e6;
     margin-top: 1rem;

     .pos{
       display: flex;
     flex-direction: row;
      gap: 0.5rem;


     h1{
       font-size: 1.2rem;
     }
     img{
       height: 100%;
       width: 1.5rem;
       border-radius: 5rem;
     }
   }

   .neg{
       display: flex;
     flex-direction: row;
    gap: 0.5rem;

     h1{
       font-size: 1.2rem;
     }
     img{
       height: 100%;
       width: 1.5rem;
       border-radius: 5rem;
     }

  }
}
  }
}
